<template><section><h1>后期处理集合</h1>
<p><code v-pre>vc-collection-stage-process-post</code> 组件用于加载后期处理集合，可以加载多组自定义着色器。注意这个组件将直接替换 <code v-pre>scene.postProcessStages</code>。</p>
<h2>示例</h2>
<h3>加载后期处理集合</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-collection-stage-process-post>
          <vc-stage-process-post :fragmentShader="fsRain"></vc-stage-process-post>
          <vc-stage-process-post :fragmentShader="fsScanSegment" :uniforms="uniforms"></vc-stage-process-post>
        </vc-collection-stage-process-post>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
