        <span class="hljs-tag">&lt;<span class="hljs-name">vc-stage-process-post</span> <span class="hljs-attr">:fragmentShader</span>=<span class="hljs-string">"fsScanSegment"</span> <span class="hljs-attr">:uniforms</span>=<span class="hljs-string">"uniforms"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-stage-process-post</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-collection-stage-process-post</span>&gt;</span>
