        <span class="hljs-keyword">var</span> _Cartesian3Center = Cesium.Cartographic.toCartesian(cartographicCenter)
        <span class="hljs-keyword">var</span> _Cartesian4Center = <span class="hljs-keyword">new</span> Cesium.Cartesian4(_Cartesian3Center.x, _Cartesian3Center.y, _Cartesian3Center.z, <span class="hljs-number">1</span>)
        <span class="hljs-keyword">var</span> _CartographicCenter1 = <span class="hljs-keyword">new</span> Cesium.Cartographic(
          cartographicCenter.longitude,
          cartographicCenter.latitude,
          cartographicCenter.height + <span class="hljs-number">500</span>
        )
        <span class="hljs-keyword">var</span> _Cartesian3Center1 = Cesium.Cartographic.toCartesian(_CartographicCenter1)
        <span class="hljs-keyword">var</span> _Cartesian4Center1 = <span class="hljs-keyword">new</span> Cesium.Cartesian4(_Cartesian3Center1.x, _Cartesian3Center1.y, _Cartesian3Center1.z, <span class="hljs-number">1</span>)
        <span class="hljs-keyword">var</span> _time = <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>().getTime()
        <span class="hljs-keyword">var</span> _scratchCartesian4Center = <span class="hljs-keyword">new</span> Cesium.Cartesian4()
        <span class="hljs-keyword">var</span> _scratchCartesian4Center1 = <span class="hljs-keyword">new</span> Cesium.Cartesian4()
        <span class="hljs-keyword">var</span> _scratchCartesian3Normal = <span class="hljs-keyword">new</span> Cesium.Cartesian3()
        <span class="hljs-keyword">this</span>.uniforms = {
          <span class="hljs-attr">u_scanCenterEC</span>: <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params"></span>) </span>{
            <span class="hljs-keyword">return</span> Cesium.Matrix4.multiplyByVector(viewer.camera._viewMatrix, _Cartesian4Center, _scratchCartesian4Center)
          },
          <span class="hljs-attr">u_scanPlaneNormalEC</span>: <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params"></span>) </span>{
